const enTxt = ()=> <div>
<p>&nbsp;&nbsp;&nbsp;The musical instrument maker Petros Zaranis, with over three decades of experience in the music industry, has been practicing the profession of instrument making since 1997. With the training he received from previous great instrument makers and skilled musicians in many fields of music science, he now handcrafts musical instruments: both professional and economical, and also accepts repairs. He now also trains some enthusiasts in this art, who help him in several constructions and repairs.</p>
<p>&nbsp;&nbsp;&nbsp;Specifically, Petros learned music from several distinguished musicians, starting with the guitar at the age of 7. Among them, his teachers were Charalampos Ekmetsoglou and Kostas Grigoreas at the "Greek Conservatory" under whom he won the first prize for classical guitar diploma '{'('}1993{')'} and then the second place in the nationwide classical guitar competition. With his teacher Christos Tziamoulis, he learned to play the oud and the wind instrument ney, which became one of his favorite musical instruments. He also studied Byzantine music with the help of P. Stratigopoulos and Tryphon Tzatsaros. Most importantly, he entered the field of instrument making with the mentorship of the famous violin and string instrument maker George Psylakis.</p>
<p>&nbsp;&nbsp;&nbsp;As for his work experience, Petros mainly constructs and repairs musical instruments such as guitar,bouzouki and oud, either by himself or as a group with his assistants. Aside from his profession as a luthier, which he has been practicing for 26 years, he is also a music teacher. He has taught in music schools in Athens, in groups of children and teenagers, as well as in individual lessons. Of course, he is also involved in music personally, studying various aspects of music and applying this knowledge to the creation of instruments capable of producing the desired sound.</p>
<p>&nbsp;&nbsp;&nbsp;Finally, he has had some artistic collaborations and compositions of his own music. Specifically, he has collaborated with big names in the music industry, such as "Charis Lambrakis, Manos Ahalinotopoulos, Nektarios Stamatelos, Nikos Paraoulakis, Manousos Klapanis, Christina Chobitaki" and released his personal album in 2011. Finally, he has performed with the band {"<<Petros Zaranis & Open Band>>"} and continues to work with other musicians.</p>
</div>

export default function InfoPage(props) {
  const [image,setImage] = React.useState(0);
  const pictures = ["../icons/magazi-mesa.jpg","../icons/guitar.jpg","../icons/voice.jpeg"]
  function next() {
    if(image+1===pictures.length) {
      setImage(0);
    } else setImage(image+1)
  }
  function prev() {
    if(image===0) {
      setImage(pictures.length-1);
    } else setImage(image-1)
  }

  return (
    <div>
      <main className="hero" style={{display: "flex", flexDirection: "row", alignItems: "center", justifyContent: "center"}}>
        <div className="someInfo">
          {props.language=='gr'?grTxt():enTxt()}
        </div>
        <div className="text--images">
          <img src="./icons/profile.jpg"></img>
          <img src="./icons/oud.jpg"></img>
        </div>
      </main>
      <div className="infoImg">
        <img id="infoPictures" src={`${pictures[image]}`}/>
        <button onClick={next} className="nextR">&#x3e;</button>
        <button onClick={prev} className="nextL">&#x3c;</button>
      </div>

    </div>
  )
}